@import url("https://fonts.googleapis.com/css2?family=Khand:wght@300;400;500;600;700&display=swap");@tailwind base;@tailwind components;@tailwind utilities;@layer base{*{font-family:'Khand', sans-serif}*,*::before,*::after{@apply p-0 m-0 select-none box-border}*::-webkit-scrollbar{@apply w-1 h-1 bg-ctcolor}html{@apply text-[62.5%]}body{@apply overflow-x-hidden overflow-y-auto scroll-smooth bg-ctbackground}}@layer components{button.pagination-btn{--rotate: 45deg;@apply relative cursor-pointer rounded-[50%] min-w-[5rem] min-h-[5rem] w-20 h-20 bg-ctcolor}button.pagination-btn::before,button.pagination-btn::after{@apply absolute left-6 right-[unset] w-[50%] h-[0.7rem] rounded-[1rem] bg-ctbackground;content:'';-webkit-transition:all 0.3s ease;transition:all 0.3s ease}button.pagination-btn::before{-webkit-transform:rotate(var(--rotate)) translateY(0.9rem);transform:rotate(var(--rotate)) translateY(0.9rem)}button.pagination-btn::after{-webkit-transform:rotate(calc(-1 * var(--rotate))) translateY(-0.9rem);transform:rotate(calc(-1 * var(--rotate))) translateY(-0.9rem)}button.pagination-btn:hover::before,button.pagination-btn:hover::after{@apply left-[1rem]}button.pagination-btn.next{--rotate: -45deg}button.pagination-btn.next::before,button.pagination-btn.next::after{@apply left-[unset] right-6}button.pagination-btn.next:hover::before,button.pagination-btn.next:hover::after{@apply right-[1rem]}div.close-btn{@apply flexcenter cursor-pointer absolute top-4 right-4 w-20 h-20}div.close-btn::before,div.close-btn::after{@apply content-[""] absolute w-[3.2rem] h-[0.4rem] bg-ctgold}div.close-btn::before{@apply rotate-45}div.close-btn::after{@apply rotate-[-45deg]}}@layer utilities{.flexcenter{@apply flex items-center justify-center}.flexcentercol{@apply flex flex-col items-center justify-center}.translate-center{top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}}
